/* w-full h-screen text-white px-4 py-4 bg-[#172b4d]  */
.bodyBoard {
    width: 100%;
    height: 100vh;
    /*   color: white; */
    padding: 1rem;
    background: #172b4d;
}

.navbar {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.navbarItem {
    background-color: rgb(255 255 255 / 0.3);
    padding: 0.25rem 0.5rem;
    margin-right: 0.5rem;
    cursor: pointer;
    text-decoration: none;
    color: white;
    min-height: 32px;
}

.navbarItem:hover {
    background-color: rgb(255 255 255 / 0.4);
}

/* flex w-full  h-[90%] lg:h-[95%] justify-start overflow-x-auto mt-4 bg-red-900 */
.containerDnd {
    display: flex;
    width: 100%;
    height: 70%;
    overflow-x: auto;
    /* background: red; */
}

@media (min-width: 1024px) {
    .containerDnd {
        height: 90%;
    }
}
