.bodyReset {
    min-width: 100vw;
    min-height: 100vh;
    background-color: #f6f6f6;
}

.loginBg {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    min-height: 100vh;
    object-fit: cover;
}

.providerBg {
    height: 3.5rem;
    width: 3.5rem;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border-radius: 4rem;
    color: white;
    cursor: pointer;
}

.providerBg:hover {
    color: white;
}

.providerIcon {
    width: 1.5rem;
    height: 1.5rem;
}

.providerBgFacebook {
    background-image: linear-gradient(
        to right,
        #0546a0 0%,
        #0546a0 40%,
        #663fb6 100%
    );
}

.providerBgInstagram {
    background-image: linear-gradient(
        to right,
        #a12ac4 0%,
        #ed586c 40%,
        #f0a853 100%
    );
}

.providerBgTwitter {
    background-image: linear-gradient(to right, #56c1e1 0%, #35a9ce 50%);
}

.providerBgGoogle {
    background-image: linear-gradient(
        to right,
        #ffffff 0%,
        #ffffff 40%,
        #ffffff 100%
    );
}

.providerBgGithub {
    background-image: linear-gradient(
        to right,
        #000000 0%,
        #000000 40%,
        #000000 100%
    );
}

.providerBgSpotify {
    background-image: linear-gradient(120deg, #1db954, #191414);
}
