.taskContainer {
    border-radius: 2px;
    padding: 8px;
    margin-bottom: 8px;
    display: flex;
    justify-content: space-between;
    background-color: white;
}

.content {
}

.icons {
    display: flex;
    justify-content: space-between;
}

.MdDelete {
}

.MdMode {
    width: 18px;
}

.hover {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    border-radius: 3px;
    color: #6b778c;
    cursor: pointer;
}

.hover:hover {
    color: #172b4d;
    background-color: #ddd;
}
