.addCard {
    padding-left: 8px;
    padding-right: 8px;
    padding-bottom: 8px;
    background-color: #ebecf0;
}

.close {
    display: flex;
    align-items: center;
    color: #6b778c;
    border-radius: 3px;
    cursor: pointer;
}

.close:hover {
    background-color: #ddd;
    color: #172b4d;
}

.MdAdd {
    width: 21px;
    height: 21px;
    margin-right: 3px;
}

.button {
    background-color: #0079bf;
    color: white;
    padding: 0.4rem 0.8rem;
    border: none;
    border-radius: 3px;
}

.button:hover {
    background-color: #026aa7;
}

.MdClose {
    color: #42526e;
    width: 25px;
    height: 25px;
    margin-left: 0.5rem;
    cursor: pointer;
}

.MdClose:hover {
    color: #172b4d;
}

.textareaTitle {
    width: 100%;
    border-radius: 3px;
    border: none;
    outline: none;
    padding: 8px;
    color: #172b4d;
    resize: none;
    margin: 0;
}
