.columnContainer {
    /* agregamos el background color white para que cuando estemos reordenando la columna no se vea que lo traspasa */
    /*  background-color: white; */
    border-radius: 2px;
    width: 220px;
    display: flex;
    flex-direction: column;
    /* background-color: #ebecf0; */
}

.columnTitle {
    padding: 8px 8px 8px 8px;
    display: flex;
    justify-content: space-between;
    background-color: #ebecf0;
}

.textareaTitle {
    width: 78%;
    cursor: pointer;
    border: none;
    outline: none;
    padding: 4px 8px;
    color: #172b4d;
    /* line-height: 16px; */
    resize: none;
    background-color: #ebecf0;
    margin: 0;
}

.textareaTitle:focus {
    background-color: white;
    /* me mueve la ui el border */
    outline: 2px solid #0079bf;
    border-radius: 3px;
}

.icons {
    display: flex;
    justify-content: space-between;
}

.MdDelete {
}

.MdPanTool {
    width: 18px;
}

.hover {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    border-radius: 3px;
    color: #6b778c;
    cursor: pointer;
}

.hover:hover {
    color: #172b4d;
    background-color: #ddd;
}

.taskList {
    /* padding: 8px; */
    /* con esto hago que ocupe todo el espacio aunque no tenga todas las tareas, facilitandome el drag an drop es decir no tenieendo que ponerlo justo debajo de la otra tarea y hace que funcione bien con la accesibilidad utilizando el teclado */
    /* flex-grow: 1; */
    /* min-height: 100px; */
    padding: 1px 8px;
    overflow-y: auto;
    background-color: #ebecf0;
}
